/* eslint-disable react/prop-types */
import React from 'react';
import moment from 'moment';
import ComponentStyles from './style/styles.module.css';

export default function RelatedEventCard({ event }) {
  const allMonths = moment.months();
  const pathname = `https://unifyhybrid.com/event/${event.name.split(' ').join('-')}-id-${event.id}`;

  return (
    <a href={pathname}>
      <div className="related_card_container">
        <img src={event.logo.url} alt="" className="related_card_img" />
        <span className="typography_spartacus_two_bold related_card_item">{event.name}</span>
        <span className="typography_spartacus_nine_demi_bold related_card_item">
          {allMonths[new Date(event.dateStart).getMonth()]}
          {' '}
          {moment(event.dateStart).format('Do')}
          ,
          {' '}
          {new Date(event.dateStart).getFullYear()}
        </span>
        <span id={ComponentStyles.related_card_item} className="typography_spartacus_six related_card_item">{event.industry.description}</span>
        <span
          className="typography_spartacus_twelve_light related_card_item related_card_info"
        >
          {event.description}
        </span>
        {/* <span id={ComponentStyles.related_card_item}>
          {event.badges && event.badges.map((badge) => renderIcon(badge.description))}
          |
          <span className="typography_spartacus_fifteen_italic"> {event.type === 'InPerson' ? 'In Person' : event.type}</span>
        </span> */}
      </div>
    </a>
  );
}
