//   const [category1, category2, category3] = keyCategories;
//   return categories.indexOf(category1) > -1 || categories.indexOf(category2) > -1 || categories.indexOf(category3) > -1;
// };

export default function Widget({ dataCategoryRss }) {
  const [loading, setLoading] = useState(true);
  const [events, setEvents] = useState([]);
  const [cardView, setView] = useState('');
  const infinite = events.length >= 4;

  const settings = {
    dots: true,
    speed: 500,
    arrows: true,
    infinite,
    slidesToShow: 4,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
        },
      },
      {
        breakpoint: 900,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 630,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  const fetchData = async () => {
    const { category, view } = JSON.parse(dataCategoryRss);
    try {
      const data = await GetEvents(category);
      setView(view);
      setEvents(data);
      setLoading(false);
    } catch (error) {
      setEvents([]);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (!events.length) {
    return <div>no events</div>;
  }

  if (events.length) {
    const eventCards = () => events.map((event) => <RelatedEventCard key={event.id} event={event} />);

    return (
      <div className="event-carousel-container">
        {cardView === 'horizontal'
          ? (
            <Slider {...settings}>
              {eventCards()}
            </Slider>
          )
          : (
            <div className="event-carousel-container-vertical">
              {eventCards()}
            </div>
          )}

      </div>
    );
  }
}
